import Component from '../base/component.js'

export default class Banner extends Component {
  constructor() {
    super();
    this.banner = document.querySelector('[data-banner]');
  }
  
  init() {
    let bannerText = this.banner.textContent;
    let wrappedLetters = bannerText.split('').map(letter => `<span class="banner-letter">${letter}</span>`).join('')

    this.banner.innerHTML = wrappedLetters;
  }

}
