import Banner from '../components/banner.js'
import Hand from '../components/hand.js'
import Cursor from '../components/cursor.js'

export default class Home {
  constructor() {
    this.components = {
      banner: new Banner(),
      hand: new Hand(),
      cursor: new Cursor()
    }
  }
  init() {
    for (let comp in this.components) {
      this.components[comp].init()
    }
  }
}
