import Component from '../base/component.js'
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
// import * as handPath from '../../gltf/hand.glb';
import anime from 'animejs/lib/anime.es.js';
import throttle from 'lodash/throttle';

// CubeMap imports
// import px from '../../img/textures/px.jpg';
// import py from '../../img/textures/py.jpg';
// import pz from '../../img/textures/pz.jpg';
// import nx from '../../img/textures/nx.jpg';
// import ny from '../../img/textures/ny.jpg';
// import nz from '../../img/textures/nz.jpg';


export default class Hand extends Component {
  // Glossy hand: https://medium.com/@soffritti.pierfrancesco/glossy-spheres-in-three-js-bfd2785d4857
  constructor() {
    super();
    this.canvas = document.querySelector('[data-hand]');
    this.fov = 10;
    this.camera = new THREE.PerspectiveCamera(this.fov, window.innerWidth / window.innerHeight, 1, 1.5 );
    this.scene = new THREE.Scene();
    this.loader = new GLTFLoader();
    this.spotLight = new THREE.SpotLight(0xffffff, .6);
    this.mesh = {};

    this.renderer = new THREE.WebGLRenderer({
      canvas: this.canvas,
      antialias: true,
      alpha: true,
      powerPreference: "high-performance",
    });

    this.renderer.gammaInput = true;
    this.renderer.gammaOutput = true;
  }

  init() {
    this.renderer.setPixelRatio(window.devicePixelRatio > 2 ? 2 : window.devicePixelRatio);

    this.camera.position.x = 1;
    this.camera.position.y = -.25;
    this.camera.position.z = .25;

    const controls = new OrbitControls(this.camera, this.canvas);

    this.spotLight.position.set(10, 1000, -10).normalize();
    this.scene.add(this.spotLight);

    const handPath = require('../../gltf/hand.glb');
    this.loader.load(handPath, this.loadMesh.bind(this));
    // this.loadMesh();

    this.on('mousemove', window, throttle(this.handleMouseMove.bind(this), 50));
    this.on('resize', window, this.setWindowSize.bind(this));
  }

  loadMesh(gltf) {
    let meshMaterial;
    // const urls = [ px, nx,
    //   py, ny,
    //   pz, nz ];

    this.mesh = gltf.scene.children[0];

    meshMaterial = this.mesh.children[1].material;
    // meshMaterial = new THREE.MeshStandardMaterial({color: 0x202020, roughness: 0});
    meshMaterial.fog = false;
    this.mesh.position.default = { x: this.mesh.position.x, y: this.mesh.position.y, z: this.mesh.position.z };

    // const cubeTexture = new THREE.CubeTextureLoader()
    // .load(
    //   urls,
    //   (cubeTexture) => {
    //     meshMaterial.envMap = cubeTexture;
    //     this.scene.add(this.mesh);
    //     this.setWindowSize();
    //     this.render();
    //   }
    //
    // );

    let envMapFile = require('../../img/envMap.png');

    let envMap = new THREE.TextureLoader().load(envMapFile);
    envMap.mapping = THREE.SphericalReflectionMapping;
    meshMaterial.envMap = envMap;
    this.scene.add(this.mesh);
    this.setWindowSize();
    this.render();
  }

  setWindowSize() {
    let fovRatio = (window.innerWidth > window.innerHeight ? window.innerWidth : window.innerHeight) / window.innerWidth
    this.camera.aspect = window.innerWidth / window.innerHeight;
    this.camera.fov = this.fov * .5 * (fovRatio + 1);

    this.camera.updateProjectionMatrix();
    this.renderer.setSize(window.innerWidth, window.innerHeight);
  }

  render() {
    this.scene.rotation.y -= 0.00125;
    this.renderer.render(this.scene, this.camera);
    requestAnimationFrame(this.render.bind(this));
  }

  handleMouseMove(e) {
    let centerX = (e.clientX - .5 * window.innerWidth);
    let centerY = (e.clientY - .5 * window.innerHeight);

    anime({
      targets: this.mesh.rotation,
      x: .00005 * centerY,
      y: .0002 * centerX,
      duration: 300,
      easing: 'easeOutQuint'
    });
  }
}
