import Component from '../base/component.js'
import anime from 'animejs/lib/anime.es.js';

export default class Cursor extends Component{
  constructor() {
    super();
    this.cursor = document.querySelector('[data-cursor]');
    this.circle = this.cursor.querySelector('[data-cursor-circle]');
    this.text = this.cursor.querySelector('[data-cursor-text]');
    this.textPath = this.cursor.querySelector('[data-cursor-text-path]');

    this.refFontSize = 16;
    this.width = 5 / 7 * this.cursor.getBoundingClientRect().width;
    this.fontSize = .2 * this.width;
    this.radius = .5 * this.width * this.refFontSize / this.fontSize;
    this.circuit = 1.75 * Math.PI * this.radius;
    this.currRotation = 0;
    this.lineIndex = 0;
    this.cursorVisible = false;

    this.anims = {}
    this.lines = [
      { text: `Hi, Fronthand here. Website is still under construction, but you can look around.` },
      { text: `Do you like the cursor? Right now it only talks a lot (just like me) but I hope it will have more tasks in the full version.` },
      { text: `Maybe you are wondering why do I even bother setting up under construction site?. At first, it helps me build brand awareness. Secondly, it allows you to contact me. And lost but not least, it helps me earn SEO points in advance. So many benefits!` },
      { text: `Ok, not much content here so I won't bore you anymore. Thanks for visiting and cross your fingers for the full version. Stay tuned!` },
      { text: `Full version soon, please come back in the nearest future.` }
    ];
  }

  init() {
    let interactives = document.querySelectorAll('a, button');

    this.getWords();
    this.getLinesLengths();
    this.setLine();

    this.on('mousemove', document, this.handleMove.bind(this));
    this.on('mousemove', document, this.show.bind(this));
    this.on('wheel', document, this.rotate.bind(this));
    this.on('mouseenter', interactives, this.expand.bind(this));
    this.on('mouseleave', interactives, this.shrink.bind(this));
  }

  getWords() {
    this.lines.forEach(line => {
      let arr = line.text.match(/([A-z|\,|\"|\“|\”|\'|\:|\?|\.|\!|\?]+)/g);

      line.words = arr.map(string => {
        return {text: string.replace(/(\.|\!|\?)$/g, `$1\xa0\xa0\xa0`)}
      });
    });
  }

  getLinesLengths() {
    let textRef = document.createElement('span');

    textRef.classList.add('label');
    textRef.classList.add('cursor-ref');
    textRef.innerHTML = this.lines.map(line => `<span class="cursor-ref-line"><span class="cursor-ref-word">${line.words.map(word => word.text).join('</span>\xa0<span class="cursor-ref-word">')}</span></span>`).join('');
    document.body.appendChild(textRef);

    textRef.querySelectorAll('.cursor-ref-line').forEach((line, i) => {
      this.lines[i].length = line.getBoundingClientRect().width * this.refFontSize / this.fontSize;
      line.querySelectorAll('.cursor-ref-word').forEach((word, j) => {
        this.lines[i].words[j].offset = (word.getBoundingClientRect().left - line.getBoundingClientRect().left)
        this.lines[i].words[j].width = (word.getBoundingClientRect().width)
      });

    });

    textRef.outerHTML = '';
  }

  setLine(index = this.lineIndex) {
    this.text.setAttribute('x', this.circuit);
    this.textPath.innerHTML = this.lines[index].words.map(word => word.text).join(' ');
    this.text.setAttribute('textLength', this.lines[index].length);
    this.textPath.setAttribute('textLength', this.lines[index].length);
  }

  show() {
    if (!this.cursorVisible) {
      anime({
        targets: this.cursor,
        opacity: 1,
        duration: 500,
        easing: 'easeOutSine'
      })

      this.animateMarquee();
      this.cursorVisible = true;
    }
  }

  rotate(e) {
    this.currRotation -= Math.abs(e.deltaY);
    this.circle.style.transform = `rotate(${.25 * this.currRotation}deg)`;
  }

  expand() {
    this.cursor.classList.add('is-expanded');
  }

  shrink() {
    this.cursor.classList.remove('is-expanded');
  }

  animateMarquee() {
    let distance = this.lines[this.lineIndex].length + this.circuit;
    let duration = distance * this.refFontSize;

    this.anims.marquee = anime({
      targets: this.text,
      x: [this.circuit, -(distance - this.circuit)],
      easing: 'linear',
      duration: duration,
      complete: () => {
        if (this.lineIndex < this.lines.length - 1) {
          this.lineIndex++;
          this.anims.marquee.x = 0;
          this.setLine();
        }

        this.animateMarquee();
      }
    })
  }

  handleMove(e) {
    this.cursor.style.transform = `translate(${e.clientX}px, ${e.clientY}px)`;
  }
}
